import React, { Component } from 'react'
import {Link} from 'react-router-dom'
export default class Contact extends Component {
  constructor(){
    super();
    this.state={
      name:'',
      email:'',
      message:'',
      ClickSend:false,
    }
  }
  onbtnSubmit=()=>{
this.setState({ClickSend:true})
  }
  onnameChange=e=>{
    this.setState({name:e.target.value})
  }
  onemailChange=e=>{
    this.setState({email:e.target.value})
  }
  onmessageChange=e=>{
    this.setState({message:e.target.value})
  }
  render() {
    const {name,email,message,ClickSend} = this.state;
    const style1 = name.length<2 ?null:({ display: "none"})
    const style2 = email.includes('@' && '.com')?({ display: "none"}):null
    const style3 = message.length<6 ?null:({ display: "none"})
    const style0 = ClickSend === false ?({ display: "none"}):null
    return (
<div>
<div style={style0}>
<div style={style1}><p className='warn-form'>{'Name Required'}</p></div>
  <div style={style2}><p className='warn-form'>{'Valid Email Required'}</p></div>
  <div style={style3}><p className='warn-form'>{'Message Required'}</p></div>
</div>
<div className="contact">
      <p className="title-contact">Contact us</p>
      <div className="contact-content">
        <form>
          <p className="title-contact-sub">Send Message</p>
          <input required onChange={this.onnameChange}
            className="F-classInput"
            type="text"
            placeholder="Name *"
          />
          <input required onChange={this.onemailChange}
            className="F-classInput"
            type="email"
            placeholder="Email *"
          />
          <textarea required onChange={this.onmessageChange}  placeholder="Message *"></textarea>
          <input onClick={this.onbtnSubmit} className="submit-btn-cntct" type="submit" value="Submit" />
        </form>
        <div className="static-contact">
          <div className="static-wraper">
            <div className="static-container-cntnt-div-specific">
              <i className="  fas fa-map-marker-alt"></i>
              <span>IU,Kushtia,Bangladesh</span>
            </div>
            <div className="static-container-cntnt-div-specific">
              <i className="  fas fa-phone-alt"></i>
              <span>+(880)1000-000000 </span>
            </div>
            <div className="static-container-cntnt-div-specific">
              <i className="  fas fa-envelope"></i>{" "}
              <span>example@example.com </span>
            </div>
            <div className="social-newtwork">
              <Link className="social-link" to="">
                <i className=" cntct-icon fab fa-facebook"></i>
              </Link>
              <Link className="social-link" to="">
                <i className=" cntct-icon fab fa-twitter"></i>
              </Link>
              <Link className="social-link" to="">
                <i className=" cntct-icon fab fa-instagram"></i>
              </Link>
              <Link className="social-link" to="">
                <i className=" cntct-icon fab fa-github"></i>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
</div>
    )
  }
}
